.saveButton {
  padding: 8px 30px;
  width: 256px;
  height: 36px;
  border-radius: 10px;
  font-family: 'Roboto' sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.23;
  text-align: center;
  color: var(--primary-white-FFFFFF);
  box-shadow: 0 4px 8px 0 rgba(64, 123, 255, 0.34);
  background: var(--primary-blue-407BFF);
}
.spinnerCss {
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (min-width: 320px) {
}
@media (min-width: 768px) {
  .saveButton {
    border-radius: 10px;
    padding: 10px 30px;
    width: 160px;
    height: 44px;
    font-family: 'Roboto' sans-serif;
    font-weight: 500;
    font-size: 18px;
  }
}
@media (min-width: 1440px) {
  .saveButton {
    border-radius: 10px;
    padding: 10px 30px;
    width: 160px;
    height: 44px;
    font-family: 'Roboto' sans-serif;
    font-weight: 500;
    font-size: 18px;
  }
}

.saveButton:hover {
  box-shadow: 0 4px 14px 0 rgba(64, 123, 255, 0.54);
  cursor: pointer;
}
